





















import { Component, Prop, Vue } from 'vue-property-decorator';
import SkillListCard from '../components/SkillListCard.vue';

interface Skill {
  name: string;
  icon: string;
  color: string;
}

@Component({
  components: {
    SkillListCard,
  }
})
export default class SkillList extends Vue{
  @Prop()
  readonly skills!: Array<Skill>;
}
