













import { Component, Prop, Vue } from 'vue-property-decorator';
import ProductListCard from '../components/ProductListCard.vue';


interface Product {
	name: string;
	techs: Array<Tech>;
	links: Array<Link>;
	memo: string;
}

interface Tech {
	icon: string;
	name: string;
}

interface Link {
	icon: string;
  color: string;
  url: string;
}


@Component({
	components: {
		ProductListCard,
	},
})
export default class SkillList extends Vue{	
	@Prop()
	products!: Array<Product>;
}
